@import '../../style/variables.scss';

.randomchar {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    align-items: center;
    box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.25);
    &__block {
        padding: 40px 35px;
        display: grid;
        grid-template-columns: 180px auto;
        column-gap: 30px;
    }
    &__img {
        width: 180px;
        height: 180px;
        object-fit: cover;
    }
    &__info {
        display: grid;
        grid-template-rows: minmax(29px, auto) 90px 38px;
        row-gap: 10px;
        padding-top: 3px;
    }
    &__name {
        font-weight: bold;
        font-size: 22px;
        line-height: 29px;
        text-transform: uppercase;
    }
    &__descr {
        font-size: 14px;
        line-height: 18px;
    }
    &__btns {
        a:nth-child(1) {
            margin-right: 30px;
        }
    }
    &__static {
        padding: 40px 35px;
        background-color: $dark;
        position: relative;
        button {
            margin-top: 13px;
        }
    }
    &__title {
        font-weight: bold;
        font-size: 24px;
        line-height: 32px;
        letter-spacing: -0.045em;
        color: #FFFFFF;
        &:nth-child(2) {
            margin-top: 33px;
        }
    }
    &__decoration {
        position: absolute;
        bottom: 14px;
        right: -25px;
    }
}

@media (max-width: 1280px) {
    .randomchar {
        &__block, &__static {
            padding: 30px 20px;
        }
    }
}

@media (max-width: 1024px) {
    .randomchar {
        grid-template-columns: repeat(1, 100%);
        &__block, &__static {
            padding: 45px 30px;
        }
        &__decoration {
            position: absolute;
            right: 0;
        }
    }
}

@media (max-width: 575px) {
    .randomchar {
        &__block, &__static {
            padding: 25px 20px;
        }
        &__block {
            grid-template-columns: repeat(1, 100%);
            min-height: 396px;
        }
        &__info {
            padding-top: 25px;
            grid-template-rows: minmax(29px, auto) auto 38px;
        }
        &__name {
            font-size: 20px;
        }
        &__img {
            width: 100%;
        }
        &__static {
            p {
                font-size: 20px;
            }
        }
        &__decoration {
            display: none;
        }
    }
}